.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.section-1 {
  font-family: "DM Sans";
  display: flex;
  flex-wrap: wrap-reverse;
  flex-direction: row;
  justify-content: center;
  background-image: linear-gradient(to top, rgba(19, 21, 25, 1), rgba(19, 21, 25, 1)), url("./assets/images/overlay.png");

