  /* background-image: linear-gradient(to bottom, rgba(119, 164, 212, 0.5), 70%, rgba(118, 92, 92, 0.5)); */
  width: 100%;
  max-width: 100%;
  height: 100%;
  -moz-align-self: center;
  -webkit-align-self: center;
  -ms-align-self: center;
  align-self: center;
  -moz-flex-grow: 1;
  -webkit-flex-grow: 1;
  -ms-flex-grow: 1;
  flex-grow: 1;
  -moz-flex-shrink: 1;
  -webkit-flex-shrink: 1;
  -ms-flex-shrink: 1;
  flex-shrink: 1;
  overflow: hidden;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
