.section-3 {
  display: flex;
  flex-wrap: wrap-reverse;
  flex-direction: row;
  justify-content: center;
  background-image: linear-gradient(to top, rgba(19, 21, 25, 0), rgba(19, 21, 25, 0)), url("./assets/images/overlay.png");

  /* background-image: linear-gradient(to bottom, rgba(118, 92, 92, 0.5), 70%, rgba(119, 164, 212, 0.5)); */
  width: 100%;
  max-width: 100%;
  -moz-align-self: center;
  -webkit-align-self: center;
